import React from 'react';

const Turnable = ({id, children, turned, containerClass}) => {
  const itemClass = `container ${containerClass} turnable ${turned ? "turned" : ""}`;
  const [front, back] = children;
  return (
    <div className={itemClass} id={id}>
      <div className="front">
        {front}
      </div>
      <div className="back">
        {back}
      </div>
    </div>
  )
}

export default Turnable;