import React, {useState, useEffect} from 'react';
import Page from '../Page';
import Cover from '../Cover';
import './styles.css';
import Turnable from './Turnable';

const lastItem = (array) => array.slice(-1)[0];

const isCover = (item) => item.type === Cover;

const BookContents = ({children}) => {
  const Empty = ({id}) => {
    return (
      <div id={id} className="container empty"/>
    )
  }

  const isEmpty = (item) => item.type === Empty;
  const Previous = ({page}) => {
    return (
      isEmpty(page) ? page : <Container key="previous" id="previous" page={page} turned={true} />
    )
  }
  const Next = ({page}) => {
    return (
      isEmpty(page) ? page : <Container key="next" id="next" page={page} turned={false} />
    )
  }

  const Container = ({page, id, turned}) => {
    return (
      <div id={id} className={`container clickable ${isCover(page) ? "cover" : "page"} ${turned ? "turned" : ""}`}>
        {page}
      </div>
    )
  }

  const items = React.Children.toArray(children);
  const lastChild = lastItem(items);

  const frontCover = isCover(items[0]) ? [items[0], <Cover />] : [items[0]];
  const backCover = isCover(lastChild) ? [<Cover />, lastChild] : [lastChild];
  const middleItems = items.slice(1, -1);

  const pages = [<Empty id="previous" />, ...frontCover, ...middleItems, ...backCover, <Empty id="next" />];
  const [currentView, setCurrentView] = useState({current: 0, flipped: false, mustFlip: false});
 
  const {current, flipped, mustFlip} = currentView;

  const [previous, front, back, next] = pages.slice(current, current + 4);

  const handleClick = (e) => {
    const clicked = e.target.closest(".container").id;
    const needsFlip = ((clicked === 'next') && !flipped) || ((clicked === 'previous') && flipped)
    if (needsFlip) {
      setCurrentView({...currentView, mustFlip: true})
    } else if ((clicked === 'next') && (current < items.length)){
      setCurrentView({current: current + 2, flipped: false, mustFlip: true});
    } else if ((clicked === 'previous') && (current > 0)) {
      setCurrentView({current: current - 2, flipped: true, mustFlip: true});
    }
  }

  useEffect(() => {
    if (mustFlip) {
      setCurrentView({...currentView, flipped: !flipped, mustFlip: false})
    }
  }, [setCurrentView, currentView, flipped, mustFlip]);

  return(
    <div className="book" onClick={handleClick}>
      <Previous page={previous} />
      <Next page={next} />
      <Turnable key={`page-${current.toString().padStart(4, '0')}`}
                id="flippable"
                containerClass={isCover(front) ? "cover" : "page"}
                turned={flipped}>
        {[front, back]}
      </Turnable> 
    </div>
  )
}

export default BookContents;