import React from 'react';
import BookContent from './BookContents';
import './styles.css';

const Book = ({children}) => {
  if (children.length === 0) {
    return (
      <div className="desk noselect empty">
        <p>No hay nada que mostrar</p>
      </div>
    )} else {
      return (
        <div className="desk noselect">
          <BookContent>
            {children}
          </BookContent>
        </div>
      )
    }
}

Book.defaultProps = {children: []};

export default Book;